._project-info {

	max-width: 740px;
	padding: 20px 40px;
	margin: 40px auto;

	code {
		font-family: consolas, menlo, monaco, monospace !important;
		font-size: 15px !important;
	}

	p {
		margin-bottom: 1em;
	}

	a {
		color: $color-primary;
		padding-bottom: 1px;
		border-bottom: 1px solid rgba($color-primary, 0);
		transition: border-bottom-color 200ms ease;
		&:hover {
			border-bottom-color: $color-primary;
		}
	}

}


._project-info__directory-list {

	list-style: none;
	li:before {
		content: "\2015";
		position: relative;
		left: -5px;
	}

	ul {
		margin-left: 20px;
		li:before {
			content: "\2013";
			position: relative;
			left: -5px;
		}
	}

}


._project-info__directory {

}


._project-info__directory-info {

}


._project-info__code-doc {

	font-size: 0;

	> div {
		display: inline-block;
		vertical-align: top;
		font-size: 16px;
		line-height: 18px;
	}

	> div:nth-of-type(1) {
		width: 25%;
	}
	> div:nth-of-type(2) {
		width: 75%;
		padding-left: 20px;
	}

}


._project-info__code-samp {

	margin-bottom: 40px;

	samp {
		display: block;
		background-color: #2B2A27 !important;
		font-family: consolas, menlo, monaco, monospace !important;
		font-size: 15px !important;
		color: #EDE0CE !important;
		padding: 15px 10px !important;
	}

	.function {
		color: #FF5D38 !important;
	}
	.string {
		color: #BCD42A !important;
	}
	.number {
		color: #BCD42A !important;
	}
	.boolean {
		color: #FF5D38 !important;
	}
	.null {
		color: #FF5D38 !important;
	}
	.key {
		color: #EDE0CE !important;
	}
	.tag {
		color: #26A6A6 !important;
	}
	.comment {
		color: #808080 !important;
	}

	p {
		margin-top: 5px;
		margin-left: 5px;
		font-size: 10px !important;
		color: #a39890;

		code {
			font-size: 10px !important;
		}
	}

}